import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PermissionsPage } from './permissions.page';
import {PermissionsEditComponent} from "./permissions-edit/permissions-edit.component";
import {PermissionsCreateComponent} from "./permissions-create/permissions-create.component";
import {PermissionsListComponent} from "./permissions-list/permissions-list.component";

const routes: Routes = [
    {
        path: '',
        component: PermissionsPage
    },
    {
        path: 'list',
        component: PermissionsListComponent
    },
    {
      path: 'create',
      component: PermissionsCreateComponent
    },
    {
        path: ':id/edit',
        component: PermissionsEditComponent
    },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [
    RouterModule
  ],
})
export class PermissionsPageRoutingModule {}
