import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {PermissionsService} from "../permissions.service";
import {ActivatedRoute} from "@angular/router";
import {Permission} from "../permission";
import {PermissionsFormComponent} from "../permissions-form/permissions-form.component";
import { Platform } from '@ionic/angular';

@Component({
    selector: 'app-permissions-create',
    templateUrl: './permissions-create.component.html',
    styleUrls: ['./permissions-create.component.scss']
})
export class PermissionsCreateComponent implements OnInit, AfterViewInit {

    @ViewChild('permissionForm') permissionForm: PermissionsFormComponent;

    id: any = null;
    public formValid = false;

    constructor(
        public permissionsService: PermissionsService,
        public route: ActivatedRoute,
        public platform: Platform
    ) {

    }

    ngOnInit(): void {
        this.clearPosts();
    }

    ngAfterViewInit() {
        this.permissionForm.permissionsForm.valueChanges.subscribe((data) => {
            this.permissionsService.permissionsFormValid.next(this.permissionForm.permissionsForm.valid);
        });
    }

    clearPosts() {
        console.log('clear');
        this.permissionsService.createPermission();
        this.permissionsService.permission = <Permission>{};
    }

}
