import {Injectable} from '@angular/core';
import {Router} from "@angular/router";
import {ApiService} from "../@shared/services/api.service";
import { Permission, PermissionLists } from "./permission";
import {Meta} from "../@shared/interfaces/meta";
import {AlertService} from "../@shared/services/alert.service";
import {ToastService} from "../@shared/services/toast.service";
import {NavigationService} from '../@shared/services/navigation.service'
import {BehaviorSubject} from "rxjs";

declare var $: any;

@Injectable({
    providedIn: 'root'
})
export class PermissionsService {

    public permissionsUrl = 'permissions';
    public permissionsFormValid = new BehaviorSubject<boolean>(false);

    public permissions: Permission[] = [];
    public permission: Permission = <Permission>{};
    public permissionLists: PermissionLists = {};

    public searchValue = '';
    public perPage = 10;

    public meta: Meta = null;

    public page = 1;

    public pagesContent = document.getElementById('pages-content');

    constructor(
        public api: ApiService,
        public alert: AlertService,
        public toast: ToastService,
        public router: Router,
        private navigation: NavigationService,
    ) {
    }

    public getPermissions(page: any = this.page, perPage: any = this.perPage) {

        this.api.get(this.permissionsUrl + '?page=' + page + '&perPage=' + perPage + '&q[name:cont]=' + this.searchValue)
            .subscribe(
                (res: any) => {
                    // @ts-ignore
                    document.getElementById('pages-content').scrollToTop(300);
                    this.permissions = res.data;
                    this.permissionLists = res.lists;
                    this.meta = res.meta;
                },
                (err: any) => {
                    console.error(err);
                }
            );
    }

    public editPermission(id: any) {
        this.api.get(this.permissionsUrl + '/' + id + '/edit')
            .subscribe(
                (res: any) => {
                    this.permission = res.data.model;
                    this.permissionLists = res.lists;
                },
                (err: any) => {
                    console.error(err);
                }
            );
    }

    public updatePermission() {
        this.api.put(this.permissionsUrl + '/' + this.permission.id, {
            model: this.permission,
            pivots: {
                role: this.permission.roles,
            }
        }).subscribe(
            (res: any) => {
                this.toast.present(res.message, 'toast-success', 'top');
                this.navigation.back();
                this.getPermissions(1);
            }
        );
    }

    public createPermission() {
        this.api.get(this.permissionsUrl + '/create')
            .subscribe(
                (res: any) => {
                    this.permission = <Permission>{};
                    this.permissionLists = res.lists
                }
            );
    }

    public storePermission() {
        this.api.post(this.permissionsUrl, {
            model: this.permission,
            pivots: {
                role: this.permission.roles,
            }
        }).subscribe(
            (res: any) => {
                this.toast.present(res.message, 'toast-success', 'top');
                this.navigation.back();
                this.getPermissions(1);
            }
        );
    }

    public async deletePermission(id: any) {
        const confirm = await this.alert.confirmation(
            'Desea eliminar el item?'
        );
        if (confirm) {
            this.api.delete(this.permissionsUrl + '/' + id, {})
                .subscribe(
                    (res: any) => {
                        this.toast.present(res.message, 'toast-success', 'top');
                        this.getPermissions(1);
                    }
                );
        } else {
            return;
        }
    }

}
