import {AfterViewInit, Component, Input, OnInit, ViewChild} from '@angular/core';
import {Permission} from "../permission";
import {FormGroup} from "@angular/forms";
import {PermissionsService} from "../permissions.service";
import {TitleCasePipe} from "@angular/common";

@Component({
    selector: 'app-permissions-form',
    templateUrl: './permissions-form.component.html',
    styleUrls: ['./permissions-form.component.scss']
})
export class PermissionsFormComponent implements OnInit, AfterViewInit {

    @Input('model') model: Permission = <Permission>{};

    @ViewChild('permissionsForm') permissionsForm: FormGroup;

    public validationMessages = {
        'name': [
            {type: 'required', message: 'El campo ' + this.toTitlecase.transform('NOMBRE') + ' es obligatorio.'},
        ],
        'code': [
            {type: 'required', message: 'El campo ' + this.toTitlecase.transform('CÓDIGO') + ' es obligatorio.'},
        ],
        'role_ids': [
            {type: 'required', message: 'El campo ' + this.toTitlecase.transform('ROLES') + ' es obligatorio.'},
        ],
    };

    constructor(
        public permissionsService: PermissionsService,
        private toTitlecase: TitleCasePipe
    ) {

    }

    ngOnInit(): void {
    }

    ngAfterViewInit() {
    }


    // set setRoleIds
    setRoleIds(event: any) {
        let roleIds = null;
        if (event.value.length) {
            roleIds = [];
            for (let i = 0; i < event.value.length; i++) {
                roleIds.push(event.value[i].id);
            }
        }
        this.permissionsService.permission.role_ids = roleIds;
    }
    // end setRoleIds


}
