import {Component, OnInit, ViewChild, AfterViewInit} from '@angular/core';
import {PermissionsService} from "../permissions.service";
import {ActivatedRoute} from "@angular/router";
import {Permission} from "../permission";
import {PermissionsFormComponent} from "../permissions-form/permissions-form.component";
import { Platform } from '@ionic/angular';

@Component({
  selector: 'app-permissions-edit',
  templateUrl: './permissions-edit.component.html',
  styleUrls: ['./permissions-edit.component.scss']
})
export class PermissionsEditComponent implements OnInit, AfterViewInit {

    @ViewChild('permissionForm') permissionForm: PermissionsFormComponent;

    public id: any = null;
    public model: Permission = null;

  constructor(
      public permissionsService: PermissionsService,
      public route: ActivatedRoute,
      public platform: Platform
  ) { }

  ngOnInit(): void {
      this.id = this.route.snapshot.paramMap.get('id');
      this.permissionsService.editPermission(this.id);
  }

  ngAfterViewInit() {
      this.permissionForm.permissionsForm.valueChanges.subscribe((data) => {
          this.permissionsService.permissionsFormValid.next(this.permissionForm.permissionsForm.valid);
      });
  }

}
